@charset "UTF-8";
/**
 * 登录页
 */
.login-body {
  background: #EF4E3C; }

.login-wrap {
  background-color: #EF4E3C; }

.login-title {
  height: 38px; }

.login-form {
  height: 475px;
  width: 990px;
  margin: 0 auto;
  position: relative;
  background: url("http://118.126.108.36/image/ui/5bf3a5d1Nbe6dc545.jpg") no-repeat center; }
  .login-form .form {
    position: absolute;
    right: 0;
    top: 50%;
    margin-top: -218px;
    height: 436px;
    width: 350px;
    background-color: white; }

.form-t {
  padding: 20px 0;
  border-bottom: 1px solid #f4f4f4; }
  .form-t::after {
    content: "";
    display: block;
    clear: both; }
  .form-t div {
    font-size: 1.6rem;
    width: 50%;
    text-align: center;
    cursor: pointer; }
    .form-t div:hover {
      color: #e4393c;
      font-weight: bold; }
  .form-t .f1 {
    color: #e4393c;
    font-weight: bold; }
  .form-t .f2 {
    color: #666; }
  .form-t .t-l {
    float: left;
    box-sizing: border-box;
    border-right: 1px solid #f4f4f4; }
  .form-t .t-r {
    float: right; }

/**
 * 手机注册页
 */
.l-input-wrap {
  width: 304px;
  margin: 20px auto; }
  .l-input-wrap label {
    width: 39px;
    height: 40px;
    float: left;
    display: block;
    border-right: 1px solid #ddd; }
  .l-input-wrap input {
    display: block;
    float: left;
    height: 38px;
    border: 0;
    padding: 0 5px;
    box-sizing: border-box; }
  .l-input-wrap > div {
    width: 306px;
    height: 40px;
    margin-bottom: 20px;
    border: 1px solid #ddd; }
  .l-input-wrap .acc-log-err {
    border: 0;
    color: red;
    text-align: right;
    height: 20px;
    margin: 0;
    margin-top: -10px;
    font-size: 12px; }
  .l-input-wrap .acc-log-err-submit {
    margin-top: 70px; }

.log-input1 input {
  width: 265px;
  outline: none; }

.account-log-submit {
  display: block;
  margin: 0 0 0 20px;
  width: 250px;
  height: 38px;
  background-color: #FF3C3C;
  font-size: 16px;
  color: #fff;
  text-align: center;
  line-height: 40px;
  border-radius: 5px;
  border: 0;
  cursor: pointer; }

.log-input2 {
  position: relative; }
  .log-input2 input {
    width: 160px;
    outline: none; }

.log-sendsms {
  width: 100px;
  height: 40px;
  position: absolute;
  right: 0;
  cursor: pointer;
  background: #ddd;
  border: none;
  color: #333;
  text-align: center;
  line-height: 40px;
  outline: none; }
