/*main主题部分*/
#main0 {
  height: 500px; }
  #main0 input {
    outline: none;
    background-color: #fff !important; }

#main1 {
  height: 500px;
  width: 350px;
  margin: 0 auto;
  margin-top: 30px; }
  #main1 li {
    position: relative; }
    #main1 li .regi-err, #main1 li .regi-hint {
      position: absolute;
      left: 380px;
      top: 0;
      padding: 5px 10px;
      line-height: 20px;
      color: #666;
      height: 40px;
      min-width: 170px;
      max-width: 200px;
      overflow: hidden;
      text-overflow: ellipsis; }
      #main1 li .regi-err::before, #main1 li .regi-hint::before {
        content: "";
        display: block;
        border-left: 0;
        border-right: 10px solid #fff4d7;
        border-bottom: 5px solid transparent;
        border-top: 5px solid transparent;
        position: absolute;
        top: 20px;
        left: -10px; }
    #main1 li .regi-err {
      background: #fff4d7; }
    #main1 li .regi-hint {
      background: #eee; }
      #main1 li .regi-hint::before {
        content: "";
        display: block;
        border-left: 0;
        border-right: 10px solid #eee;
        border-bottom: 5px solid transparent;
        border-top: 5px solid transparent;
        position: absolute;
        top: 20px;
        left: -10px; }

