.regi-success {
  width: 800px;
  margin: 30px auto;
  border: 1px solid #ccc;
  padding: 10px 30px;
  background-color: #e4f5e9; }
  .regi-success::after {
    content: "";
    display: block;
    clear: both; }
  .regi-success .rs-tit-logo {
    float: left;
    width: 125px;
    height: 125px;
    margin-left: 20px; }
  .regi-success .rs-in-wrap {
    float: left;
    margin-left: 40px; }
  .regi-success .rs-tit::after {
    content: "";
    display: block;
    clear: both; }
  .regi-success .rs-tit-info {
    float: left;
    font-size: 20px;
    font-weight: 600;
    color: #666; }
  .regi-success .rs-logname, .regi-success .rs-nickname {
    margin-left: 200px;
    font-size: 15px;
    color: #666;
    margin-top: 10px; }
  .regi-success .rs-logname-imp {
    color: #f40;
    font-weight: bold; }
  .regi-success .rs-operator {
    margin-left: 100px;
    margin-top: 20px; }
    .regi-success .rs-operator::after {
      content: "";
      display: block;
      clear: both; }
  .regi-success .rs-to-log, .regi-success .rs-to-home {
    float: left;
    font-size: 14px;
    font-style: italic; }
    .regi-success .rs-to-log a:hover, .regi-success .rs-to-home a:hover {
      color: #f40;
      text-decoration: underline; }
  .regi-success .rs-to-home {
    margin-left: 100px; }
  .regi-success .rs-to-log {
    margin-left: 40px; }
    .regi-success .rs-to-log a {
      color: #f40; }
